// import { notFound } from "next/navigation"; // Not strictly needed if we just show empty state, but good for 404s if desired. Let's keep empty state for now or 404 if no category matches? Detailed req says "showcasing different projects".
import { notFound } from "next/navigation";
import ProjectGrid from "@/components/ProjectGrid";
import { getProjects } from "@/app/actions/projects";
import { slugify } from "@/lib/utils";

interface CategoryPageProps {
    params: Promise<{ category: string }>;
}

export async function generateStaticParams() {
    const projects = await getProjects();
    const uniqueCategories = Array.from(new Set(projects.map(p => p.category)));

    return uniqueCategories.map((category) => ({
        category: slugify(category),
    }));
}

export default async function CategoryPage({ params }: CategoryPageProps) {
    const { category } = await params;

    const allProjects = await getProjects();

    // Find projects where slugified category matches the param
    const filteredProjects = allProjects.filter(p => slugify(p.category) === category);

    // If no projects found for this category slug, it might be a true 404
    if (filteredProjects.length === 0) {
        // Optional: could check if category exists at all in DB, but filtering result is 0 is effectively same for user
        // notFound(); 
        // Or just show empty state as per previous file
    }

    return (
        <main className="min-h-screen pt-32 pb-20 px-4 md:px-10">
            <div className="relative z-10">
                <ProjectGrid projects={filteredProjects} />

                {filteredProjects.length === 0 && (
                    <div className="text-white font-mono text-center pt-20">
                        No se encontraron proyectos en esta categor√≠a.
                    </div>
                )}
            </div>
        </main>
    );
}

